.skillsDetails{
    width: 85%;
    margin: 3% auto 1%;
    box-shadow: 0px 5px 15px 5px #bbbbbb;
}
h1{
    padding: 0;
    margin: 0;
    font-family: 'Bona Nova', serif;
    font-size: 25px;
    letter-spacing: 2px;
    padding: 2.5% 0 0 2.5%;
}

.frontEnd {
    width: 90%;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    height: 30vh;
    grid-gap: 20px;
    align-items: center;
    justify-content: center;
    text-align: center;
}
.child{
    height: 25vh;
    box-shadow: 0px 5px 10px 1px lightgray;
}
.child > img {
    width: 80%;
    height: 70%;
    padding-top: 6%;
}
.github-calendar{
    width: 85%;
    margin: 3% auto 1%;
}
#statistics{
    width: 85%;
    margin: 3% auto 1%;
}
.main-stat{
    display: flex;
    width: 100%;
    margin: auto;
    flex-direction: column;
    text-align: center;
}
.stacistics-container{
    width: 90%;
    margin: auto;
    justify-content: center;
    margin: 2rem auto;
}
.stacistics-box{
    display:flex;
    justify-content: space-around;
}
.statisticsTitle > h1 {
    font-weight: 700;
    margin-top: 100px;
    position: relative;
    width: fit-content;
}
.statisticsTitle > h1::after {
    background-color: #506aff;
    content: " ";
    height: 20%;
    position: absolute;
    left: -5px;
    top: 55%;
    width: calc(100% + 10px);
    z-index: -1;
}
.individualStat {
    width: 140px;
    text-align: center;
}
.individualStat > div:nth-child(1) {
    margin: auto;
    font-size: 28px;
    font-weight: 600;
    height: 130px;
    width: 130px;
    border: 10px solid #506aff;
    border-radius: 65px;
    display: flex;
    justify-content: center;
    align-items: center;
}

